@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.navbar {
    display: flex;
    flex-direction: row;
    font-family: 'Montserrat', sans-serif;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-color: white;
}

.menu-button{
    position: fixed;
    width: 10vw;
    top:20px;
    left:20px;
    z-index: 100;
}

.icon {
    img {
        object-fit: cover;
        width: 15vw;
        max-height: 80px;
        margin: 20px 40px;
    }
}

.nav-content {
    height: 100%;
    width: 40%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 5vw;
    padding-top: 2vh;
}

.dropdown {
    position: relative;
    display: inline-block;

}


.dropdown-content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100vw;
    top: 200%;
    left: 50%;
    background-color: white;
    transform: translate(-50%, 0);
    box-shadow: 2px 2px 2px 0px #0000000f;
    z-index: 1;
    height: 0%;

    overflow: hidden;
    transition: height 0.3s ease-in-out;
}

.dropdown-content.open {
    height: 300%;
}

.nav-button {
    cursor: pointer;

    position: relative;
}

.nav-button:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.nav-button:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0px;
    left: 0;
    background-color: #047ad2;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.nav-button:active {
    color: #223a60;
}

.benefit-button {
    padding: 0 20px;
    position: relative;
    cursor: pointer;
    color: #047ad2;
}

.benefit-button.end {
    border: 0;
}

.benefit-button:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.benefit-button:after {
    content: '';
    position: absolute;
    width: calc(100% - 40px);
    ;
    transform: scaleX(0);
    height: 2px;
    bottom: 0px;
    left: 20px;

    background-color: #51def7;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.icon-content {
    width: 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 2vh;
    font-size: 25px;
    height: 100%;
    margin-left: 20vw;
    color: #223a60;
}

.brand-icon {
    cursor: pointer;
}

.brand-icon:active {
    color: #047ad2;
}